/**
 * 
 * 此文档包含以下接口
 *      获取顶级菜单接口
 *      获取子级菜单接口
 *      获取角色接口
 *      新建角色接口
 * 
 * 
 * 
 * 
 * 
 * 权限模块
 * 可以在这里定义一个ip地址 如果有ip地址用此ip地址，没有则使用config中的url
 */

import axios from '../axios';
import config from '../config'

// 下面是一个做注释的例子
/**
 * 获取某一个部门所有的人  ----------- 此处书写接口的作用 
 * @params page {String} 页码   ------- 此处书写  @params 参数名 {类型} 描述
 * 
 * @params obj {Object} 请求体
 *  obj.uid {String} 用户id
 *  obj.data {String} 发送呢绒
 *  obj.company_id {String} 
 */
// export const getDepAllMember = (obj) => {
//   return axios(__handleReqIp({

//     method: 'GET',
//     url: '/aaa/dep',
//     params: {
//       uid: obj.uid,
//       data: obj.data
//     }
//   }));
// };

function axiosApi(obj) {
    return axios({
        method: obj.method,
        configURL: config.authorizedURL,
        url: obj.url ? obj.url : '/zero-box/jurisdiction',
        params: obj.params,
        data: obj.data,
        headers: obj.headers
    });
}

/**
 * 获取顶级菜单接口
 * @param {*} obj 
 * obj.api {string} API接口 *
 * obj.company_id {string} 公司ID *
 */
export const getTopMenu = (obj) => {
    return axiosApi({
        params: {
            api: 'zero.box.jurisdiction.menu.getAll',
            company_id: obj.company_id
        }

    });
}

/**
 * 获取子级菜单接口
 * @param {*} obj 
 * obj.api {string} API接口 *
 * obj.company_id {string} 公司ID *
 * obj.menuid {string} 上一级ID *
 */
export const getSublevelMenu = (obj) => {
    return axiosApi({
        params: {
            api: 'zero.box.jurisdiction.menu.getSonAll',
            company_id: 'cp_001' || obj.company_id,
            menuid: obj.menuid
        }

    });
}

/**
 * 请求获取角色接口
 * @param {*} obj 
 * obj.api {string} API接口 *
 * obj.company_id {string} 公司ID *
 * obj.user_id {string} 当前用户ID *
 * obj.pageIndex {string} 当前页码 *
 * obj.pageSize {string} 每页显示多少条 *
 */
export const getRole = (obj) => {
    return axiosApi({
        params: {
            api: 'zero.box.jurisdiction.role.get',
            company_id: obj.company_id,
            user_id: obj.user_id,
            pageIndex: obj.pageIndex,
            pageSize: obj.pageSize
        }
    })
}

/**
 * 新建角色接口
 * @param {*} obj 
 * obj.api {string} API接口 *
 * obj.company_id {string} 公司ID *
 * obj.userName {string} 角色名称 *
 * obj.description {string} 角色描述 *
 * obj.menus {Array} 角色权限 *
 * obj.alias {string} 角色别名
 */

export const addRole = (obj) => {
    return axiosApi({
        method: 'POST',
        params: {
            api: 'zero.box.jurisdiction.role.add',
            company_id: 'cp_001',
        },
        data: {
            _name: obj.userName,
            description: obj.description,
            menus: obj.menus,
            alias: obj.alias
        }
    })
}


/**
 * 为角色新增菜单权限
 * 
 *  obj.api {string} API接口 *
 * obj.company_id {string} 公司ID *
 * obj.userName {string} 角色名称 *
 * obj.menus {Array}  菜单集合
 * obj.role_id {String}  角色ID
 */

export const addMenu = (obj) => {
    return axiosApi({
        method: 'POST',
        params: {
            api: 'zero.box.jurisdiction.role.addMenu',
            company_id: obj.company_id,
        },
        data: {
            menus: obj.menus,
            role_id: obj.role_id
        }
    })
}

/**
 * 新建角色与用户关联条目
 * obj.company_id {String} 企业id
 * obj.role_id  {String}  角色id
 * obj.user_id  {Array}   用户id
 */
export const addUserandrole = (obj) => {
    return axiosApi({
        method: 'POST',
        params: {
            api: 'zero.box.jurisdiction.userandrole.add',
            company_id: obj.company_id,
        },
        data: {
            users: obj.users,
            role_id: obj.role_id
        }
    })
}


/**
 * 新增用户组与角色关联条目
 * 
 * obj.company_id {String} 企业id
 * obj.role_id  {String}  角色id
 * obj.team_id  {Array}   部门id
 * 
 */
export const addTeamandrole = (obj) => {
    return axiosApi({
        method: 'POST',
        params: {
            api: 'zero.box.jurisdiction.teamandrole.add',
            company_id: obj.company_id,
        },
        data: {
            role_id: obj.role_id,
            teams: obj.teams
        }
    })
}

/**
 * 获取单个角色的权限明细
 * obj.company_id {String} 企业id
 * obj.roleid  {String}  角色id
 * obj.user_id  {Array}   用户id
 */
export const getOne = (obj) => {
    return axiosApi({
        // method: 'POST',
        params: {
            api: 'zero.box.jurisdiction.role.getOne',
            company_id: obj.company_id,
            user_id: obj.user_id,
            roleid: obj.roleid
        }
    })
}


/**
 * 修改角色信息
 * obj.company_id {String} 企业id
 * obj.user_id  {Array}   用户id 
 * 
 * obj.roleid  {String}  角色id
 * obj._name   {String}  角色名称
 * obj.alias   {String}  角色别名
 * obj.description {string} 角色描述 
 * obj.menus {Array} 角色权限 
 */

export const reviseRole = (obj) => {
    return axiosApi({
        method: 'POST',
        params: {
            api: 'zero.box.jurisdiction.role.update',
            company_id: obj.company_id,
            user_id: obj.user_id
        },
        data: {
            role_id: obj.roleid,
            _name: obj.name,
            alias: obj.alias,
            description: obj.description,
            menus: obj.menus
        }
    })
}


/**
 * 角色换取用户与部门接口
 * 
 * obj.company_id {String} 企业id
 * obj.roleid  {String}  角色id
 * obj.user_id  {Array}   用户id
 * 
 */

export const getRolePower = (obj) => {
    return axiosApi({
        params: {
            api: 'zero.box.jurisdiction.role.roleid2userOfteam',
            company_id: obj.company_id,
            user_id: obj.user_id,
            role_id: obj.role_id,
        }
    })
}

/**
 * 删除角色
 * 
 * obj.company_id {String} 企业id
 * obj.roleid  {String}  角色id
 * obj.user_id  {Array}   用户id
 */
export const deleteRole = (obj) => {
    return axiosApi({
        method: 'POST',
        params: {
            api: 'zero.box.jurisdiction.role.delete',
            company_id: obj.company_id,
            user_id: obj.user_id,
            role_id: obj.role_id,
        }
    })
}

/**
 * 删除角色与用户与部门权限关系
 * @param {*} obj 
 * obj.company_id {String} 企业id
 * obj.roleid  {String}  角色id
 * 
 * @data {*} obj
 * obj.users    {Array}  
 */
export const unbindRoleOfTeamsAndUsers = (obj) => {
    return axiosApi({
        method: 'POST',
        params: {
            api: 'zero.box.jurisdiction.role.unbindRoleOfTeamsAndUsers',
            company_id: obj.company_id,
            user_id: obj.user_id
        },
        data: {
            users: obj.users,
            teams: obj.teams,
            role_id: obj.role_id
        }
    })
}