/**
 * 文件管理模块 API
 * @author Zyc
 */
import axios from '../axios'
import config from '../config'

/**
 * 修改默认请求配置文件
 * @author Zyc
 */

/*
  此文档包含以下接口
    上传App安装包
    上传图片
    上传base64位的图片换取地址
*/

/**
 * 修改默认请求配置文件
 */
const axiosAPI = function (obj) {
  return axios({
    configURL: config.filesURL,
    url: obj.url ? obj.url : '/zero-box/file',
    method: obj.method,
    params: obj.params,
    data: obj.data
  });
};

/**
 * 上传App安装包
 * @params obj {Object}
 *  obj.company_id {String} 公司id *
 *  obj.user_id {String} 用户id *
 *  app_file {file 文件类型} 文件包 FormData 文件*
 */
export const uploadAppPackage = (obj) => {
  return axiosAPI({
    method: 'POST',
    header: {
      'Content-Type': 'multipart/form-data'
    },
    params: {
      api: 'zero.box.files.app.uploader',
      company_id: obj.company_id,
      user_id: obj.user_id
    },
    data: obj.app_file
  });
};

/**
 * 上传图片
 * @params obj {Object}
 *  obj.company_id {String} 公司id *
 *  obj.user_id {String} 用户id *
 *  file {file} 图片 可同时上传多个 *
 */
export const uploadImg = (obj) => {
  return axiosAPI({
    method: 'POST',
    header: {
      'Content-Type': 'multipart/form-data'
    },
    params: {
      api: 'zero.box.files.img.uploader',
      company_id: obj.company_id,
      user_id: obj.user_id,
    },
    data: obj.file
  });
};


/**
 * 上传base64位的图片换取地址
 * @params obj {Object}
 *  obj.company_id {String} 公司id *
 *  obj.user_id {String} 用户id *
 *  obj.base64s {Array} *
 */
export const base64toUrl = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.files.img.base64s2img',
      company_id: obj.company_id,
      user_id: obj.user_id,
    },
    data: {
      base64s: obj.base64s
    }
  });
};