/**
 * 通讯录模块 API
 * @author Zyc
 */
import axios from '../axios'
import config from '../config'
/*
  此文档包含以下接口
    新建企业
    新建部门
    查询企业 名称 和 id
    根据企业和部门id获取部门下方所有的部门和人员
    获取部门下的子部门
    获取部门中的所有成员
    切换 用户的启用 禁用状态
    删除用户
    删除部门
    修改部门名称
    请求某一个用户的详细信息
*/

/*
  第一个企业 
    {
      企业id: 5d1463a61609d536e0bf2a1e, 移动公司
        移动技术事业部门id: 60454: {
          人员  937741304
        }
    }

    {
      企业id: 5d157efb756ea88810f3c1f4, 电信公司
        电信公司通讯录id: 14021: {
        电信开发部： 74186: {
          张四一:  937741304
        }
      }
    }

*/

/**
 * 修改默认请求配置文件
 */
const axiosAPI = function (obj) {
  return axios({
    configURL: config.addressBookURL,
    url: obj.url ? obj.url : '/zero-box/mailList',
    method: obj.method,
    params: obj.params,
    data: obj.data
  });
};

/**
 * 新建企业
 * @params obj {Object} 请求体
 *  obj.name {String} 企业名称 
 */
export const addCompanya = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.mailList.add_companya'
    },
    data: {
      name: obj.name
    }
  });
};

/**
 * 新建部门
 * @params obj {Object} 
 *  obj.name {String} 部门名称 *
 *  obj.company_id {String} 公司 id *
 *  obj.parentId {String} 父级部门 id 除了一级部门之外其他的都需要此参数
 */
export const addDepartment = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.mailList.add_department'
    },
    data: {
      name: obj.name,
      company_id: obj.company_id,
      parentId: obj.parentId
    }
  });
};

/**
 * 查询企业 名称 和 id
 * @params obj {Object} 
 *  obj.company_id {String} 公司id
 */
export const getCompony = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.mailList.dep_list',
      company_id: '5d1463a61609d536e0bf2a1e',
      pageIndex: 1,
      pageSize: 10
    }
  });
};

/**
 * 根据企业和部门id获取部门下方所有的部门和人员
 * @params obj {Object} 
 *  obj.company_id {String} 企业id
 *  obj.depid {String} 部门id 不传值返回所有成员
 */
export const getDepAllMemeber = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.mailList.find_dep_info',
      company_id: '5d1463a61609d536e0bf2a1e',
      depid: obj.depid
    }
  });
};

/**
 * 获取部门下的子部门
 * @params {Object} 
 *  obj.company_id {String} 公司id
 *  obj.depid {String} 部门id
 */
export const getChildrenDep = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.mailList.dep_list',
      company_id: '5d1463a61609d536e0bf2a1e',
      depid: obj.depid
    }
  });
};

/**
 * 获取部门中的所有成员
 * @params obj {Object} 
 *  obj.company_id {String} 公司id
 *  obj.pageIndex {Number} 页码
 *  obj.pageSize {Number} 分页 一页多少个
 *  obj.depid {String} 部门id
 */
export const getDepAllMember = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.mailList.user_list',
      company_id: '5d1463a61609d536e0bf2a1e',
      pageIndex: 1,
      pageSize: 10,
      depid: obj.depid
    }
  });
};

/**
 * 切换 用户的启用 禁用状态
 * @params obj {Object} 
 *  obj.company_id {String} 公司id
 *  obj.userid {String} 用户id
 *  obj.type {Number} 禁用类型 1表示禁用 2表示启用
 */
export const toggleEnableStatus = (obj) => {
  console.log('禁用时的参数');
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.mailList.enable'
    },
    data: {
      company_id: obj.company_id,
      userid: obj.userid,
      type: obj.type
    }
  });
};

/**
 * 删除用户
 * @params obj {Object} 
 *  obj.company_id {String} 公司id
 *  obj.userid {String} 用户账号 批量删除，逗号隔开，例如zhangsan,zhangsan1
 */
export const deleteUserByUserid = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.mailList.del_user'
    },
    data: {
      company_id: obj.company_id,
      userid: obj.userid
    }
  });
};

/**
 * 删除部门
 * @params {Object} obj
 *  obj.company_id {String} 公司id
 *  obj.depid {String} 部门id
 */
export const deleteDep = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.mailList.del_department'
    },
    data: {
      company_id: obj.company_id,
      depid: obj.depid
    }
  });
};

/**
 * 修改部门名称
 * @params {Object} obj
 *  obj.company_id {String} 企业id
 *  obj.depid {String} 修改的部门id
 *  obj.name {String} 修改过后的名字
 *  obj.parentId {String} 要删除的当前部门的父级部门id
 */
export const updateDepName = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.mailList.update_department'
    },
    data: {
      company_id: obj.company_id,
      depid: obj.depid,
      name: obj.name,
      parentId: obj.parentId
    }
  });
};

/**
 * 查询某一个用户所在的所有部门 详细信息
 * @params {Object} obj
 *  obj.company_id {String} 企业id
 *  obj.userid {String} 用户id
 *  obj.isName {Number} 是否获取部门的详细信息， 1为是 不获取则不传此参数
 *  obj.type {String} // 0代表获取用户顶级分组 1代表获取分组顶级分组
 */
export const getUserAllDep = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.mailList.find_group',
      company_id: obj.company_id,
      type: 0, 
      userid: obj.userid,
      isName: obj.isName
    }
  });
};


/**
 * 查询某一个用户的详细信息
 * @params {Object} obj
 *  obj.company_id {String} 企业id
 *  obj.userid {String} 用户id
 */
export const getUserDetail = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.mailList.find_user',
      company_id: obj.company_id,
      userid: obj.userid
    }
  });
};

/**
 * 用户分组操作
 * @params {Object} obj
 *  obj.company_id {String} 企业id * 
 *  obj.userid {String} 用户id  * 
 *  obj.type {String} 操作类型 0新增分组 1移除分组 2 更换分组
 *  obj.depid {String} 分组id 移除多个或增加多个,逗号隔开 23039,23039
 *  obj.from {String} 原属分组 type==3时触发
 *  obj.to {String} 替换分组 type==3时触发
 */
export const changeUserInDep = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.mailList.info_group'
    },
    data: {
      company_id: obj.company_id,
      userid: obj.userid,
      type: obj.type,
      depid: obj.depid,
      from: obj.from,
      to: obj.to
    }
  });
};
