import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/index'
import './assets/css/common.css'

// 引入 element-ui 待到生产环境将所需的组件按需引入 减小体积 ---->>>>>> 将自己用到的组件在下方打上注释
/*   */
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

import CHeader from './components/c_header.vue'
import CSideBar from './components/c_sidebar.vue'
import CContentHeader from './components/c_contentHeader.vue'


import api from './API/index.js' // 请求接口模块
import ZERO from './API/zero' // 全局公有方法
Vue.prototype.$api = api;
Vue.prototype.$zero = ZERO;


// 经常使用的组件 设置为全局组件 不用引入 直接使用
Vue.component('CHeader', CHeader); // 后台UI顶部
Vue.component('CSideBar', CSideBar); // 后台UI侧边栏
Vue.component('CContentHeader', CContentHeader); // 后台UI内容顶部


Vue.use(ElementUI);
Vue.config.productionTip = false

// 在这里 取一下 session 有的 没有话


// 全局修改页面标题
router.beforeEach((to, from , next) => {
  if(to.meta.title){
    document.title = to.meta.title;
  }
  next();
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
