/**
 * 应用模块 API
 */
import axios from '../axios'
import config from '../config'


/**
 * 此文档包括接口
 * 
 * 获取平台列表
 * 获取新建的几种类型
 * 新建类型
 * 获取应用列表
 * 新建Andriod应用
 * 新建IOS应用
 * 新建H5应用
 * 新建小程序应用
 * 获取某一个应用的详情
 * 删除应用
 * 发布应用
 * 应用停用
 * 修改应用信息
 * 设置应用主页
 * 设置可信域名SDK
 */

/**
 * 修改默认请求配置文件
 */
const axiosAPI = function (obj) {
  return axios({
    configURL: config.applicationURL,
    url: obj.url ? obj.url : '/zero-box/application',
    method: obj.method,
    params: obj.params,
    data: obj.data
  });
};

/**
 * 获取平台列表
 * @params obj {Object} 请求体
 *  obj.company_id {String} 企业id 
 */
export const getPlatformList = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.application.platform.get',
      company_id: obj.company_id
    }
  });
};

/**
 * 获取新建的几种类型
 * @params {Object} obj
 *  obj.company_id {String} 企业id
 */
export const getTypeList = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.application.type.get',
      company_id: obj.company_id
    }
  });
};

/**
 * 新建类型
 * @params {Object} obj
 *  obj.company_id {String} 企业id
 *  obj._name {String} 类型名称
 *  obj.level {Number} 层级 0
 */
export const createNewType = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.application.type.create',
      company_id: obj.company_id
    },
    data: {
      _name: obj._name,
      level: obj.level
    }
  });
};

/**
 * 获取应用列表
 * @params {Object} obj
 *  obj.company_id {String} 企业id
 *  obj.platform_alias {String} 类型别名 通过查询获取平台列表中的别名 我的企业应用的别名 SMALL H5 IOS ANDROID
 *  obj.pageIndex {Number} 当前页
 *  obj.pageSize {Number} 一页查询多少个应用 默认15
 *  obj.status {Satus} 查询状态 0开发中 1已经上线 2停用
 */
export const getAppList = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.application.app.get',
      company_id: obj.company_id,
      platform_alias: obj.platform_alias,
      pageIndex: obj.pageIndex,
      pageSize: obj.pageSize || 15,
      status: obj.status,
      user_id: obj.user_id
    }
  });
};

/**
 * 新建Andriod应用
 * @params {Object} obj
 *  obj.company_id {String} 企业id *
 *  obj._name {String} 应用名称 *
 *  obj.typeid {String} 类型id 新建的类型的id 查询类型列表可获取id *
 *  obj.icon {String} url地址 通过将logo图片上传服务器获取得到的url地址 *
 *  obj.description {String} 应用描述信息 *
 *  obj.founder {String} 创建人id  用户的 userid *
 *  obj.manages {Array String} 管理员   eg: ['user01', 'user01']; *
 *  obj.allow_ranges {Array} 可见范围 *
 *  obj.agentid {String} 应用包的agentid *
 *  obj.build {String} 打包了多少次吧 参数 build 次数  eg: "build 11" *
 *  obj.storage_url {String} 应用包地址 通过文件模块上传的应用包返回的 http链接地址 *
 *  obj.version {String} app的版本号 *
 *  obj.size {Number} 应用包的大小 最多保留两位小数吧  eg: 14.2M *
 *  obj.update_description {String} 更新描述 *
 *  obj.package_type {String} 什么类型  eg：企业版 *
 *  obj.previews {Array} 预览图地址
 */
export const createAndriod = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.application.app.createAndroid',
      company_id: obj.company_id
    },
    data: {
      _name: obj._name,
      typeid: obj.typeid,
      icon: obj.icon,
      description: obj.description,
      founder: obj.founder,
      allow_ranges: obj.allow_ranges,
      manages: obj.manages,
      agentid: obj.agentid,
      previews: obj.previews,
      build: obj.build,
      storage_url: obj.storage_url,
      version: obj.version,
      size: obj.size,
      update_description: obj.update_description,
      package_type: obj.package_type
    }
  });
};

/**
 * 新建IOS应用
 * @params {Object} obj
 *  obj.company_id {String} 企业id *
 *  obj._name {String} 应用名称 *
 *  obj.typeid {String} 类型id 新建的类型的id 查询类型列表可获取id *
 *  obj.icon {String} url地址 通过将logo图片上传服务器获取得到的url地址 *
 *  obj.description {String} 应用描述信息 *
 *  obj.founder {String} 创建人id  用户的 userid *
 *  obj.manages {Array String} 管理员   eg: ['user01', 'user01']; *
 *  obj.allow_ranges {Array} 可见范围 *
 *  obj.agentid {String} 应用包的agentid *
 *  obj.build {String} 打包了多少次吧 参数 build 次数  eg: "build 11" *
 *  obj.storage_url {String} 应用包地址 通过文件模块上传的应用包返回的 http链接地址 *
 *  obj.version {String} app的版本号 *
 *  obj.size {Number} 应用包的大小 最多保留两位小数吧  eg: 14.2M *
 *  obj.update_description {String} 更新描述 *
 *  obj.package_type {String} 什么类型  eg：企业版 *
 */
export const createIOS = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.application.app.createIos',
      company_id: obj.company_id
    },
    data: {
      _name: obj._name,
      typeid: obj.typeid,
      icon: obj.icon,
      description: obj.description,
      founder: obj.founder,
      allow_ranges: obj.allow_ranges,
      manages: obj.manages,
      agentid: obj.agentid,
      build: obj.build,
      storage_url: obj.storage_url,
      source_name: obj.source_name,
      // obj. storage_url source_name
      version: obj.version,
      size: obj.size,
      update_description: obj.update_description,
      package_type: obj.package_type,
      previews: obj.previews,
      factory: "5d15b2924e53838850469509"
    }
  });
};

