<template>
<!-- 
  isShowIcon 是否显示返回按钮
  back 父组件监听 back 返回按钮点击事件
  content 显示内容
 -->
  <div class="c_content_header flex flex-item">
    <i v-if="isShowIcon" @click="backBtn" class="el-icon-arrow-left"></i>
    <p class="cch_con">{{content}}</p>
  </div>
</template>
<script>
export default {
  props: {
    // 是否显示 返回按钮
    isShowIcon: {
      type: Boolean,
      default: false
    },
    // 内容
    content: {
      type: String,
      required: true
    }
  },
  data() {
    return {};
  },
  methods: {
    /**
     * 点击返回按钮给父组件传值
     */
    backBtn() {
      // console.log('1111');
      window.history.go(-1);
    }
  }
};
</script>
<style scoped>
.c_content_header{
  height: 50px;
  padding-left: 40px;
  border-bottom: 1px solid #e6e6e6;
  background: #fff;
}
.el-icon-arrow-left{
  padding-top: 2px;
  font-size: 22px;
  color: #2f78ef;
  font-weight: bold;
  cursor:pointer;
}
.cch_con{
  font-weight: bold;
}

</style>
