/**
 * 初始化项目，这里是此项目中的公有方法
 */
import url from 'url'
import Vue from 'vue'
import globalData from './globalData'
const vm = new Vue(); // 获取 Vue 实例化对象 拿到 Element-ui 中的 $message 中的方法


export default {
    /**
     * eventBus.API
     *  跨越Vue中父子组件传值的局限性，这里定义的事件可以兄弟组件、父孙组件等等传值。
     *  详细用法 查阅  https://segmentfault.com/a/1190000018520676
     */
    eventBus: vm,

    /**
     * 成功、警告、消息、错误
     */

    // @params msg {String} 成功提示消息
    ToastSuccess(msg) {
        vm.$message({
            message: msg,
            type: 'success'
        });
    },

    // @params msg {String} 警告提示消息
    ToastWarning(msg) {
        vm.$message({
            message: msg,
            type: 'warning'
        });
    },

    // @params msg {String} 消息提示
    ToastMessage(msg) {
        vm.$message(msg);
    },

    // @params msg {String} 错误消息提示
    ToastError(msg) {
        vm.$message.error(msg);
    },

    /**
     * 带有icon的不会自动关闭的提示 此处用于 上传app的过程中对上传结果进行提示
     * 
     * @param {String} 类型 success warning info error 四种类型 默认成功 
     * @params {String} title 标题
     * @params {String} message 提示内容
     * @params {NUmber} duration 持续时间 默认3s 传 0 则不关闭
     */
    notify({ type = 'success', title, message, duration = 3000 }) {
        vm.$notify({ title, message, type, duration });
    },

    /**
     * 全局loadding 用于上传时间过长而且不允许动用其他的地方
     * @param {Object} obj 对象
     *  obj.text 提示内容
     * 
     * @return {Object} loadding 对象，通过此lodding对象调用 loading.close(); 关闭
     */
    wholeLoadding({ text }) {
        return vm.$loading({
            lock: true,
            text: text,
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
        });
    },

    //删除前提示
    DeleteTips(title) {
        return new Promise((resolve, reject) => {
            vm.$confirm(title, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                resolve(true);
            }).catch(() => {
                // reject(false)
                // resolve(false)
            });
        })

    },

    // 清除登录信息
    clearLoginInfo() {
        // this.removeLocalStorageItem('user');
        // this.removeSessionStorage('user');
    },

    // 切割url传参的内容
    parseUrl(urlString) {
        return url.parse(urlString, true).query;
    },


    // 生成图片地址
    fileToBlobUrl(file) {
        if (window.URL) {
            return window.URL.createObjectURL(file);
        } else if (window.webkitURL) {
            return window.webkitURL.createObjectURL(file);
        } else {
            return null;
        }
    },

    // cookie的操作
    // time 单位年
    setCookie(json, time) {
        let timer = new Date(Date.now() + time * 365 * 24 * 60 * 60 * 1000).toUTCString();
        for (let key in json) {
            document.cookie = key + '=' + json[key] + ';expires=' + timer;
        }
    },

    /**
     * 获取Cookie
     * @params {String} attr
     */
    getCookie(attr) {
        let arr = document.cookie.match(new RegExp('\\b' + attr + '=([^;]+)(;|$)'));
        return arr ? arr[1] : "";
    },

    /**
     * 删除Cookie
     * @params {String} attr
     */
    removeCookie(attr) {
        let json = {};
        json[attr] = '';
        this.setCookie(json, -1);
    },


    // 封装 LocalStorage 数据
    /**
     * 封装 localstorage 的一些方法
     * @params {Object json}
     */
    setLocaStorage(json) {
        for (let key in json) {
            let val = JSON.stringify(json[key]);
            window.localStorage.setItem(key, val);
        }
    },

    /**
     * 获取 localStorage 的一些方法
     * @params {String} key
     */
    getLocalStorageItem(key) {
        return JSON.parse(window.localStorage.getItem(key));
    },

    /**
     * 删除 localStorage 的某一项
     * @params {String} key
     */
    removeLocalStorageItem(key) {
        window.localStorage.removeItem(key);
    },

    // 清空 localStorage 的数据
    clearLocalStorageItem() {
        window.localStorage.clear();
    },

    // SessionStorage的一些方法
    /**
     * SessionStorage中的一些东西
     * @params {String}
     */
    getSessionStorage(key) {
        return JSON.parse(window.sessionStorage.getItem(key));
    },

    /**
     * 设置 SessionStorage 中的一些东西
     * @params {Object-json} json
     */
    setSessionStorage(json) {
        for (let key in json) {
            let val = JSON.stringify(json[key]);
            window.sessionStorage.setItem(key, val);
        }
    },

    /**
     * 删除一个 SessionStorage方法
     * @params {String} key
     */
    removeSessionStorage(key) {
        window.sessionStorage.removeItem(key);
    },
    /**
     * 清空 Session 中的值
     */
    clearSessionStorage() {
        window.sessionStorage.clear();
    },

    /**
     * 格式化日期
     * @params date {String} Tue May 14 2019 10:25:22 GMT+0800 (中国标准时间)
     * @return 2019-5-14 10:26:11
     */
    formatDate(date) {
        return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
    },

    /**
     * 格式化日期 startDate
     * 返回今天的  00:00:00
     */
    formatStartDate(date) {
        return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} 00:00:00`;
    },
    /**
     * 格式化日期
     * 返回今天的 23:59:59
     */
    formatEndtDate(date) {
        return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} 23:59:59`;
    },

    formatWantTime(date) {
        return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}`;
    },

    // 验证身份证号码
    regIdCard(idCard) {
        return /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/.test(
            idCard
        );
    },

    // 验证手机号码是否合法
    regPhone(phone) {
        return /^1[34578]\d{9}$/.test(phone);
    },

    // 验证邮箱是否合法
    regEmail(email) {
        return /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(email);
    },

    // 验证一个字符串是否是 HTTP 网址
    // @return {Boolean}
    resHTTPStr(url) {
        return /^([hH][tT]{2}[pP]:\/\/|[hH][tT]{2}[pP][sS]:\/\/)(([A-Za-z0-9-~]+).)+([A-Za-z0-9-~\/])+$/.test(url);
    },

    // 获得globalData整体
    getApp() {
        return globalData;
    },

    // 获取用户 uid
    getUserid() {
        return "5d4b93a32027b3512c00a1e4" || globalData.userInfo.userid || null;
    },

    // 获取用户信息
    getUserInfo() {
        return globalData.userInfo;
    },

    // 获取企业id
    getCompany_id() {
        return '5d1463a61609d536e0bf2a1e' || globalData.companyInfo.company_id || null;
    }
}