<template>
    <div class="common_sidebar" :class="{hide: isCollapse}">
        <el-row class="tac">
            <el-col :span="12">
                <!-- @open="handleOpen"
        @close="handleClose"-->
        <!-- :router="true"  -->
                
                <el-menu :default-active="this.$route.path" class="el-menu-vertical-demo" background-color="#2b2e33" text-color="#999999" :collapse="isCollapse" router @select="handleSelect" active-text-color="#ffffff">
                    <el-menu-item index="/dataOverview">
                        <img v-if="defaultSelect === '/dataOverview'" class="sidebar_icon" :src="require('../assets/img/sidebar/1.png')">
                        <img v-else class="sidebar_icon" :src="require('../assets/img/sidebar/1-1.png')">
                        
                        <span slot="title">数据总览</span>
                    </el-menu-item>
                    <el-menu-item index="/addressBook">
                        <img v-if="defaultSelect === '/addressBook'" class="sidebar_icon" :src="require('../assets/img/sidebar/2.png')">
                        <img v-else class="sidebar_icon" :src="require('../assets/img/sidebar/2-1.png')">
                        <span slot="title">通讯录</span>
                    </el-menu-item>
                    <el-menu-item index="/appmanage">
                        <img v-if="defaultSelect === '/appmanage'" class="sidebar_icon" :src="require('../assets/img/sidebar/3.png')">
                        <img v-else class="sidebar_icon" :src="require('../assets/img/sidebar/3-1.png')">
                        <span slot="title">应用管理</span>
                    </el-menu-item>
                    <el-menu-item index="/workbench">
                        <img v-if="defaultSelect === '/workbench'" class="sidebar_icon" :src="require('../assets/img/sidebar/4.png')">
                        <img v-else class="sidebar_icon" :src="require('../assets/img/sidebar/4-1.png')">
                        <span slot="title">工作台</span>
                    </el-menu-item>
                    <el-menu-item index="/procedure">
                        <img v-if="defaultSelect === '/procedure'" class="sidebar_icon" :src="require('../assets/img/sidebar/5.png')">
                        <img v-else class="sidebar_icon" :src="require('../assets/img/sidebar/5-1.png')">
                        <span slot="title">流程控制</span>
                    </el-menu-item>
                    <el-menu-item index="/power">
                        <img v-if="defaultSelect === '/power'" class="sidebar_icon" :src="require('../assets/img/sidebar/6.png')">
                        <img v-else class="sidebar_icon" :src="require('../assets/img/sidebar/6-1.png')">
                        <span slot="title">权限管理</span>
                    </el-menu-item>
                    <el-menu-item index="/journal">
                        <img v-if="defaultSelect === '/journal'" class="sidebar_icon" :src="require('../assets/img/sidebar/7.png')">
                        <img v-else class="sidebar_icon" :src="require('../assets/img/sidebar/7-1.png')">
                        <span slot="title">日志管理</span>
                    </el-menu-item>
                    <el-menu-item index="/personInfo">
                        <img v-if="defaultSelect === '/personInfo'" class="sidebar_icon" :src="require('../assets/img/sidebar/8.png')">
                        <img v-else class="sidebar_icon" :src="require('../assets/img/sidebar/8-1.png')">
                        <span slot="title">用户信息</span>
                    </el-menu-item>
                </el-menu>
            </el-col>
        </el-row>
        <div class="sidebar_hide" @click="toggleMenu">
            <img :src="require('../assets/img/sidebar/toggle.png')">
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                defaultSelect: "",
                menuList: [],
                isCollapse: false // 默认展示
            };
        },
        methods: {
            // 显示隐藏左侧菜单栏
            toggleMenu(){
                this.isCollapse = !this.isCollapse;
                this.$emit('sidebar', this.isCollapse);
            },
            // handleOpen(key, keyPath) {
            //   console.log(key, keyPath);
            //   // console.log(key, keyPath);
            // },
            // handleClose(key, keyPath) {
            //   console.log(key, keyPath);

            //   // console.log(key, keyPath);
            // },

            // 点击左侧导航触发函数 修改颜色
            handleSelect(path) {
                console.log('修改字体颜色。。。。。');
                console.log(path);
                
                this.changeSideBarColode(path);
            },

            /**
             * 修改左侧导航的颜色
             * @params path {String} 无参数取路由地址，有参数取参数
             */
            changeSideBarColode(path) {
                path = path ? path : ('/'+ this.$router.history.current.path.split("/")[1]);
                // console.log(path);
                this.defaultSelect = path;
            }
        },
        created() {
            this.changeSideBarColode();
        }
    };
</script>
<style>
    .common_sidebar {
        height: 100%;
    }

    .common_sidebar .el-row {
        height: 100%;
    }

    .common_sidebar .el-col-12 {
        width: 100%;
        height: 100%;
    }

    .common_sidebar .el-menu-vertical-demo {
        height: 100%;
    }
    .sidebar_item{
        color: #000;
    }
    .sidebar_icon {
        width: 18px;
        height: 18px;
        margin: 0 15px 0 5px;
    }
    .common_sidebar .el-menu{
        border-right: none;
    }
    .common_sidebar .sidebar_hide{
        position: absolute;
        bottom: 0;
        left: 0;
        height: 50px;
        display: flex;
        align-items: center;
        cursor: pointer;
    }
    .common_sidebar .sidebar_hide img{
        padding-left: 19px;
    }
    .common_sidebar.hide .el-menu-item span{
        opacity: 0;
    }
</style>