import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [{
        path: '/',
        name: 'home',
        component: Home
    },
    {
        path: '/about',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].API) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import( /* webpackChunkName: "about" */ './views/About.vue')
    },
    {
        path: '/public',
        name: 'public',
        component: () =>
            import( /* webpackChunkName: "about" */ './pages/q_public.vue'),
        children: [{
            path: '/index',
            name: 'index',
            component: () => import( /* webpackChunkName: "about" */ './pages/q_index.vue'),
            meta: { title: '首页' }
        },
        {
            path: '/login',
            name: 'login',
            component: () => import( /* webpackChunkName: "about" */ './pages/q_login.vue'),
            meta: { title: '登录' }
        },
        {
            path: '/appmarket',
            name: 'appmarket',
            component: () => import( /* webpackChunkName: "about" */ './pages/q_appmarket/q_appmarket.vue'),
            meta: { title: '应用市场' }
        }

        ]
    },
    {
        path: '/addressBook1', // 通讯录
        name: 'addressBook1',
        component: () => import( /* webpackChunkName: "about" */ './views/c_addressBook/c_template.vue'),
        children: [{
            path: '/addressBook',
            name: 'addressBook',
            component: () => import( /* webpackChunkName: "about" */ './views/c_addressBook/addressBook.vue'),
            meta: { title: '通讯录' }
        },
        {
            path: '/test',
            name: 'test',
            component: () => import('./views/c_addressBook/test.vue')
        },
        { //数据总览1
            path: '/dataOverview',
            name: 'dataOverview',
            component: () => import('./views/c_dataOverview/dataOverview.vue'),
            meta: { title: '数据总览' }
        },
        { //权限管理
            path: '/power',
            name: 'power',
            component: () => import('./views/c_power/power.vue'),
            meta: { title: '权限管理' }
        },
        { //权限管理——新建角色
            path: '/powerCreateRoles',
            name: 'powerCreateRoles',
            component: () => import('./views/c_power/powerCreateRoles.vue'),
            meta: { title: '角色创建' }
        },
        { //权限管理---修改角色
            path: '/powerModify/:id',
            name: 'powerModify',
            component: () => import('./views/c_power/powerModify.vue'),
            meta: { title: '修改角色' }
        },
        { //工作台
            path: '/workbench',
            name: 'workbench',
            component: () => import('./views/c_workbench/workbench.vue'),
            meta: { title: '工作台' }
        },
        { //日志管理
            path: '/journal',
            name: 'journal',
            component: () => import('./views/c_journal/journal.vue'),
            meta: { title: '日志管理' }
        },
        { //流程管理
            path: '/procedure',
            name: 'procedure',
            component: () => import('./views/c_procedure/procedure.vue'),
            meta: { title: '流程管理' }
        },
        { //流程管理-流程设置1
            path: '/procedureSetTemplate',
            name: 'procedureSetTemplate',
            component: () => import('./views/c_procedure/procedureSetTemplate.vue'),
            meta: { title: '流程设置' }

        },
        { //流程管理-流程设置2-配置审批人
            path: '/procedureSetRuleSettings',
            name: 'procedureSetRuleSettings',
            component: () => import('./views/c_procedure/procedureSetRuleSettings.vue'),
            meta: { title: '配置审批人' }

        },
        { //流程管理-条件审批流程
            path: '/procedureApprovalCondition',
            name: 'procedureApprovalCondition',
            component: () => import('./views/c_procedure/procedureApprovalCondition.vue'),
            meta: { title: '条件审批流程' }
        },
        { //流程管理-APP上架申请
            path: '/procedureEdit/:name/:id',
            name: 'procedureEdit',
            component: () => import('./views/c_procedure/procedureEdit.vue'),
            meta: { title: '流程管理' },
            children: [{
                path: '/procedureEdit/:name/:id',
                redirect: '/procedureEdit/ruleTemplate/:name/:id'
            },
            { //流程管理-APP上架申请-流程设置
                path: '/procedureEdit/ruleTemplate/:name/:id',
                name: 'ruleTemplate',
                component: () => mport('./views/c_procedure/ruleTemplate.vue'),
                meta: { title: '流程管理' }
            },
            { //流程管理-APP上架申请-流程设置
                path: '/procedureEdit/ruleSettings/:name/:id',
                name: 'ruleSettings',
                component: () => import('./views/c_procedure/ruleSettings.vue'),
                meta: { title: '流程管理' }
            }
            ]
        },
        {
            path: '/personInfo',
            name: 'personInfo',
            component: () => import('./views/c_personInfo/personInfo.vue'),
            meta: { title: '用户信息' },
        },
        {
            path: '/changePassword',
            name: 'changePassword',
            component: () => import('./views/c_personInfo/changePassword.vue'),
            meta: { title: '修改密码' }
        },
        {
            path: '/authInfo',
            name: 'authInfo',
            component: () => import('./views/c_personInfo/authInfo.vue'),
            meta: { title: '认证信息' }
        },
        {
            path: '/appmanage',
            name: 'appmanage',
            redirect: '/appmanages/SMALL',
            component: () => import('./views/c_appmanage/appmanage.vue'),
            meta: { title: '应用管理' },
            children: [{
                path: '/appmanages/:id',
                name: 'appmanageList',
                component: () => import('./views/c_appmanage/appmanageList.vue'),
                meta: { title: '应用列表' }
            },
                // {
                //     path: '/appmanage/andriod',
                //     name: 'appmanageAndriod',
                //     component: () => import('./views/c_appmanage/appmanageAndriod.vue'),
                //     meta: { title: 'Andriod应用' },
                // },
                // {
                //     path: '/appmanage/Ios',
                //     name: 'appmanageAndrIos',
                //     component: () => import('./views/c_appmanage/appmanageIos.vue'),
                //     meta: { title: 'Ios应用' },
                // },
                // {
                //     path: '/appmanage/small',
                //     name: 'appmanageSmall',
                //     component: () => import('./views/c_appmanage/appmanagesmall'),
                //     meta: { title: '小程序应用' }
                // }
            ]
        },
        {
            path: '/appmanage/detail',
            name: 'appmanageDetail',
            component: () => import('./views/c_appmanage/appDetail.vue'),
            meta: { title: '应用详情' }
        },
        {
            path: '/appmanage/createAndriod',
            name: 'createAndriod',
            component: () => import('./views/c_appmanage/createAndriod.vue'),
            meta: { title: '新建Andriod应用' }
        },
        {
            path: '/appmanage/createH5',
            name: 'createH5',
            component: () => import('./views/c_appmanage/createH5.vue'),
            meta: { title: '新建H5应用' }
        },
        {
            path: '/appmanage/createIos',
            name: 'createIos',
            component: () => import('./views/c_appmanage/createIos.vue'),
            meta: { title: '新建Ios应用' }
        },
        {
            path: '/appmanage/createSmall',
            name: 'createSmall',
            component: () => import('./views/c_appmanage/createSmall.vue'),
            meta: { title: '新建小程序' }
        },
            // {
            //     path: '/md5',
            //     name: 'md5',
            //     component: () => import('./views/c_appmanage/md5.vue')
            // }
        ]
    }
    ]
})